/* Color pallet */
/* Font size */
/* Font weight */
/* Font family */
/* Design styling */
/* Media breakpoint */
/*
  key points: 576 - xs, 768 -sm , 992 - md, 1200 - lg
*/
font-face {
  font-family: "Maven Pro", sans-serif;
  font-style: normal;
  font-weight: 500;
  src: local("Maven Pro"), url("../fonts/mavenpro/Mavenpromedium.woff2") format("woff2"), url("../fonts/mavenpro/Mavenpromedium.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "Maven Pro", sans-serif;
  font-style: normal;
  font-weight: 700;
  src: local("Maven Pro"), url("../fonts/mavenpro/Mavenprobold.woff2") format("woff2"), url("../fonts/mavenpro/Mavenprobold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat"), url("../fonts/montserrat/montserratbold.woff2") format("woff2"), url("../fonts/montserrat/montserratbold.woff") format("woff");
  font-display: swap;
}
/*
  key points: 576 - xs, 768 -sm , 992 - md, 1200 - lg
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Maven Pro", "Arial", sans-serif;
  font-weight: 500;
  background-color: #f0f1f1;
}

body {
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}
a:hover, a:focus {
  text-decoration: none;
}

img {
  max-width: 100%;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.container {
  width: 100%;
  max-width: 74.25rem;
  height: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.reset-button {
  margin: 0;
  padding: 0;
  width: auto;
  font: inherit;
  text-align: inherit;
  line-height: normal;
  background: transparent;
  border: none;
  color: inherit;
  overflow: visible;
  outline: none;
}
.reset-button :focus:not(:focus-visible) {
  outline: none;
}
.reset-button :focus:not(:-moz-focusring) {
  outline: none;
}

/* Page layout */
/* Component layout */
.cards {
  columns: 4 263px;
  column-gap: 30px;
  padding: 3.13rem 1rem 7.5rem;
}
@media (max-width: 47rem) {
  .cards {
    padding: 3.13rem 1rem;
  }
}
.cards__add-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10.94rem;
  border: 2px solid #fdfdfd;
  color: #fdfdfd;
  cursor: pointer;
}
@media (max-width: 35rem) {
  .cards__add-button {
    height: 3.75rem;
  }
}
.cards__add-button:hover, .cards__add-button:focus {
  border: 2px solid #e8c5ed;
  color: #e8c5ed;
}

.card__item {
  display: flex;
  flex-direction: column;
  margin-top: 1.88rem;
  margin-bottom: 1.88rem;
  box-shadow: 0px 0px 20px #e0e1e5;
  break-inside: avoid;
}
.card__item--small {
  height: 10.94rem;
}
.card__item--large {
  height: 21.88rem;
}
.card__item--white {
  background-color: #ffffff;
}
.card__item--pink {
  background-color: #fdf1ff;
}
.card__period {
  margin: 0;
  padding: 1.25rem 1.25rem 0 1.25rem;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 0.83;
  color: #6e6e6e;
  text-transform: uppercase;
}
.card__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.card__image--small {
  height: 10.88rem;
}
.card__image--large {
  height: 15.94rem;
}
.card__description {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  justify-content: flex-end;
  padding: 1.25rem;
}
.card__title {
  margin: 0 0 0.75rem 0;
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 0.83;
  color: #6e6e6e;
}
.card__subtitle {
  display: flex;
  justify-content: space-between;
}
.card__date {
  flex: 1 0 auto;
  font-size: 1rem;
  line-height: 0.84;
  color: #484b98;
}
.card__time {
  font-size: 1rem;
  line-height: 0.84;
  color: #484b98;
  text-transform: uppercase;
}
.card__link {
  margin-top: auto;
  font-size: 1rem;
  line-height: 0.67;
  color: #6e6e6e;
  text-decoration: none;
}
.card__link:hover, .card__link:focus {
  color: #484b98;
}

.form {
  width: 100%;
  padding: 2.5rem 0 2.38rem;
  background-color: #fdfdfd;
  border-radius: 2px;
  box-shadow: 0 0 20px #fdfdfd;
}
.form__wrapper {
  display: grid;
  grid-template-columns: max-content 1fr 1fr;
  column-gap: 30px;
}
@media (max-width: 47rem) {
  .form__wrapper {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 30px;
  }
}
.form__checkbox {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-template-rows: repeat(2, 1fr);
  column-gap: 50px;
  row-gap: 18px;
}
@media (max-width: 47rem) {
  .form__checkbox {
    grid-template-columns: repeat(2, 1fr);
    grid-column: 1/2;
    order: 2;
  }
}
@media (max-width: 35rem) {
  .form__checkbox {
    grid-column: 1/-1;
  }
}
.form__group {
  display: flex;
  gap: 30px;
}
@media (max-width: 61rem) {
  .form__group {
    align-items: baseline;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
  }
}
@media (max-width: 47rem) {
  .form__group {
    align-items: center;
    order: 3;
  }
}
@media (max-width: 35rem) {
  .form__group {
    grid-column: 1/-1;
    flex-direction: row;
  }
}
.form__select {
  width: 10.31rem;
  height: 2.5rem;
}
@media (max-width: 35rem) {
  .form__select {
    width: 8.75rem;
  }
}
.form__switcher {
  min-width: 10.31rem;
  height: 2.5rem;
}
.form__search {
  height: 2.5rem;
}
@media (max-width: 47rem) {
  .form__search {
    grid-column: 1/-1;
    order: 1;
  }
}

.footer {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  height: 5.63rem;
  background-color: #484b98;
}
@media (max-width: 47rem) {
  .footer {
    padding: 0;
    height: 2.75rem;
  }
}
.footer__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 47rem) {
  .footer__wrapper {
    padding: 0;
  }
}

.header {
  position: relative;
  min-height: 3.13rem;
  background-color: #484b98;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  position: relative;
  margin: 0;
  margin-bottom: 0.38rem;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 2.19rem;
  line-height: 0.83;
  color: #ffffff;
  text-transform: lowercase;
}
.logo::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 0.75rem;
  height: 0.75rem;
  margin-top: -0.38rem;
  margin-left: 0.19rem;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (max-width: 47rem) {
  .logo--footer {
    display: none;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.nav__item {
  list-style-type: none;
}
.nav__item:not(:first-child) {
  margin-left: 3.25rem;
}
.nav__link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Maven Pro", "Arial", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 0.84;
  color: #ffffff;
  text-decoration: none;
  transition: all 500ms ease;
  white-space: nowrap;
}
.nav__link:hover, .nav__link:focus {
  color: #e8c5ed;
}
.nav__icon {
  width: 1rem;
  height: 1rem;
  margin-right: 0.25rem;
}
@media (max-width: 47rem) {
  .nav--header {
    display: none;
  }
}
.nav--header .nav__link--active {
  position: relative;
}
.nav--header .nav__link--active::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.13rem;
  background-color: currentColor;
  transform: translateY(100%);
  transition: all 500ms ease;
}
.nav--header .nav__icon {
  display: none;
}
@media (max-width: 47rem) {
  .nav--footer {
    width: 100%;
    height: 100%;
  }
  .nav--footer .nav__item {
    flex-grow: 1;
    margin: 0;
  }
  .nav--footer .nav__link {
    padding: 0.75rem 0;
  }
  .nav--footer .nav__link--active {
    background-color: #383b83;
    transition: all 500ms ease;
  }
  .nav--footer .nav__link--active:hover, .nav--footer .nav__link--active:focus {
    background-color: rgba(56, 59, 131, 0.5);
  }
}
.nav--footer .nav__icon {
  display: none;
}
@media (max-width: 47rem) {
  .nav--footer .nav__icon {
    display: block;
  }
}

.socials {
  display: none;
  align-items: center;
  justify-content: center;
  list-style: none;
}
@media (max-width: 47rem) {
  .socials {
    position: absolute;
    right: 0;
    bottom: 2.75rem;
    flex-direction: column;
    background-color: #383b83;
  }
}
.socials--open {
  display: flex;
}
.socials__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
@media (min-width: 48rem) {
  .socials__item {
    margin-left: 2.88rem;
  }
}
.socials__item--dropdown {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.88rem;
  height: 100%;
  margin-left: 0;
  background-color: rgba(253, 241, 255, 0.2);
}
@media (min-width: 48rem) {
  .socials__item--dropdown {
    display: none;
  }
}
.socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.81rem;
  height: 2.81rem;
  color: #fdfdfd;
  transition: color 250ms ease;
}
.socials__link:hover, .socials__link:focus, .socials__link:active {
  color: #e8c5ed;
}
@media (max-width: 47rem) {
  .socials .socials__icon {
    font-size: 1rem;
  }
}

.select {
  width: 100%;
  height: 100%;
  padding: 0 2.25rem 0 1rem;
  font-family: inherit;
  font-size: inherit;
  line-height: 0.89;
  appearance: none;
  background: #e7e7ea url("../../../images/arrow.svg") calc(100% - 4px) 4px no-repeat;
  border: none;
  border-radius: 2px;
  color: #6e6e6e;
  cursor: pointer;
  outline: none;
}
.select:focus {
  box-shadow: 0px 0px 20px #e0e1e5;
}

.switcher {
  display: flex;
  border: 4px solid #e7e7ea;
}
.switcher:focus {
  box-shadow: 0px 0px 20px #e0e1e5;
}
.switcher__input:checked + label {
  background-color: #484b98;
  color: #ffffff;
}
.switcher__label {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 0 4.88rem;
  height: 2rem;
  padding: 0.5rem;
  cursor: pointer;
}
.switcher__label-major {
  background-color: #fdf1ff;
  color: #6e6e6e;
}
.switcher__label-major:hover, .switcher__label-major:focus {
  background-color: #e8c5ed;
}
.switcher__label-minor {
  background-color: #ffffff;
  color: #6e6e6e;
}
.switcher__label-minor:hover, .switcher__label-minor:focus {
  background-color: #c8c9d9;
}

.search {
  width: 100%;
  padding: 0.44rem 0;
  padding-left: 1.88rem;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  text-transform: capitalize;
  background: url("../../../images/search.svg") 0% center/24px 24px no-repeat transparent;
  border: none;
  border-bottom: 1px solid #e7e7ea;
  color: #6e6e6e;
  transition: padding-left 250ms ease;
}
.search::-webkit-search-decoration {
  display: none;
}
.search::placeholder {
  color: #e7e7ea;
}
.search:active, .search:focus {
  padding-left: 0.38rem;
  outline: 0;
  background-image: none;
}

.checkbox {
  position: relative;
  padding-left: 2.25rem;
  line-height: 0.89;
  color: #6e6e6e;
  cursor: pointer;
}
.checkbox:hover .checkbox__mark, .checkbox:focus .checkbox__mark, .checkbox:active .checkbox__mark {
  background-color: #e8c5ed;
}
.checkbox__input:checked + .checkbox__mark {
  background-color: #fdf1ff;
}
.checkbox__input:checked + .checkbox__mark::after {
  display: block;
}
.checkbox__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 1.25rem;
  height: 1.25rem;
  background-color: #e7e7ea;
  border-radius: 2px;
}
.checkbox__mark:hover, .checkbox__mark:focus, .checkbox__mark:active {
  background-color: #e8c5ed;
}
.checkbox__mark::after {
  content: "";
  display: none;
  width: 0.69rem;
  height: 0.5rem;
  margin-top: 0.25rem;
  margin-left: 0.31rem;
  border: 1px solid #484b98;
  border-width: 0 0 1px 1px;
  transform: rotate(-45deg);
}